<template>
    <div class="get-phone-box flex-ajc w100">
      <form class="phone-wrap w-90 bgf fz15">
        <div class="form-group">
          <input type="tel" placeholder="请输入手机号" maxlength="11" class="w100">
        </div>
        <div class="form-group flex-space-between">
          <input type="tel" placeholder="请输入验证码" maxlength="6" style="width: 54%;">
          <a href="javascript:void(0)" class="w-40 btn tc" :class="showCode ? 'btn-code-default' : 'btn-code-orange'">获取验证码</a>
        </div>
      </form>
    </div>
</template>

<script>
  var store = {};
  var method = {};

  store.showCode = false;

  export default {
    data(){
      return store;
    },
    methods:method,
    mounted(){

    }
  }
</script>

<style scoped lang="scss">
 .get-phone-box{
   position: fixed;
   bottom: 0;
   top: 0;
   left: 0;
   right: 0;
   background-color: rgba(51,51,51,0.6);
   z-index: 999999;
 }

 .phone-wrap{
   padding: 2.5rem 2rem;
   z-index: 9999;
   input{
     border: none;
     padding:1.3rem 1.5rem;
     background-color: #f7f7f7;
     border-radius: 0.3rem;
   }
   .form-group{
     margin-bottom: 2.5rem;
   }

 }
  .btn{
    padding:1.2rem 0.8rem;
    border-radius: 0.3rem;
  }
 .btn-invite-default{
   background-color: #cecece;
   pointer-events: none;
 }
 .btn-code-orange{
   background-color: #ffd102;
 }
</style>



