<template>
  <div class="wechat-lottery-container w100 h100 oh-y-a">
    <div class="img-box">
      <a href="javascript:void (0);">
       <img src="../../../assets/images/wechat/invite/lottery.png" alt="">
      </a>
    </div>
    <div style="padding: 0 1.5rem;">
      <router-link :to="{path:'/regWorker',query:{workerID:workerID,mobile:inviter}}" class="wechat-btn-invite tac fz21 also-join">我也要加入住建鸟</router-link>
    </div>
    <div class="mh3 lottery-wrap">
      <div class="lottery-box">
        <div class="flex-space-between">
          <i class="line"></i>
          <p class="fz20 white">中奖名单</p>
          <i class="line"></i>
        </div>
        <div class="scroll-lottery-box mt2 oh pr">
          <ul class="scroll-lottery" :class="{'scroll-list' : len > 3}">
            <li class="flex-space-between fz17" v-for="item in luckLists" :key="item.id">
              <p class="white w30 tl" v-cloak>{{item.workerName}}</p>
              <p class="white w30 tc">抽到</p>
              <p class="cyellow w30 tr">{{item.goods}}</p>
            </li>
            <li class="flex-space-between fz17" v-show="luckLists[0]">
              <p class="white w30 tl" v-cloak>{{luckLists[0].workerName}}</p>
              <p class="white w30 tc">抽到</p>
              <p class="cyellow w30 tr">{{luckLists[0].goods}}</p>
            </li>
            <li class="flex-space-between fz17"  v-show="luckLists[1]">
              <p class="white w30 tl" v-cloak>{{luckLists[1].workerName}}</p>
              <p class="white w30 tc">抽到</p>
              <p class="cyellow w30 tr">{{luckLists[1].goods}}</p>
            </li>
            <li class="flex-space-between fz17"  v-show="luckLists[2]">
              <p class="white w30 tl" v-cloak>{{luckLists[2].workerName}}</p>
              <p class="white w30 tc">抽到</p>
              <p class="cyellow w30 tr">{{luckLists[2].goods}}</p>
            </li>
          </ul>
        </div>

      </div>
      <div class="lottery-box mt3">
        <div class="flex-space-between">
          <i class="line"></i>
          <p class="fz20 white">活动简介</p>
          <i class="line"></i>
        </div>
        <p class="fz15 white intr-text">
          新工友注册即可获得千元购物红包！假一赔百好保障！邀请朋友还有百元现金奖励哦！快来加入吧！
        </p>
      </div>
    </div>
  </div>
</template>

<script>

  import api from '../../../services/api';
  import error from '../../../services/error'

  var store = {};
  var method = {};

  store.luckLists = [];
  store.len = '';
  store.workerID = '';
  store.inviter = '';
  store.money = '' ;
  store.number = '';

  //中奖列表
  method.luckyMans = function () {
    api.luckList().then(function (res) {
      if (res.code == error.success)
      console.log(res);
      store.luckLists = res.result;
      store.len = store.luckLists.length;
      method.keyFrame();
      console.log(store.len);
    })
  };
  //动态计算keyframe
  method.keyFrame = function () {
    console.log("key");
    var style = document.styleSheets[0];
    style.insertRule("@keyframes dd { 0%{transform: translateY(0rem);} 100%{transform: translateY(-"+(store.len *3)+"rem);} }");
    style.insertRule(".scroll-list{animation: dd "+store.len *1+"s linear infinite normal;animation-fill-mode: forwards;}");
  };

  //微信分享的方法
  method.weChatShare = function () {
    try {
      api.share({url:window.location.href}).then(function (response) {
        const  result = response.result;
        const config = {
          debug: false,
          appId: result['appid'],
          timestamp: result['timestamp'],
          nonceStr: result['nonceStr'],
          signature: result['signature'],
          jsApiList: ['onMenuShareAppMessage', 'onMenuShareTimeline']
        };
        console.log("config",config);



        wx.config(config);
        wx.ready(function () {
          store.money = store.vm.$route.query.money ;
          store.number = store.vm.$route.query.number ;
          var shareData = {
            title: '【住建鸟】又拿了'+store.money+'元现金红包，我已经有'+store.number+'个兄弟加入了你也快来吧！',
            desc: '有活干，有钱赚，假一赔百好保障！',
            link: window.location.href,
            imgUrl: 'http://dist.zjbird.com/icon/red_packet.png',
            success:function () {
              console.log("分享成功了");
            }
          };
          console.log("shareData",shareData);
          wx.onMenuShareAppMessage(shareData);
          wx.onMenuShareTimeline(shareData);
        });
        wx.error(function(res){
          console.log(res)
        });
      })
    }catch (e) {
      console.log(e.message);
    }
  };
  export default {
    name: "lottery",
    data(){
      return store;
    },
    methods:method,
    mounted(){
      store.vm = this;

      store.workerID = store.vm.$route.query.workerID ;
      store.inviter = store.vm.$route.query.mobile ;
      store.money = store.vm.$route.query.money ;
      store.number = store.vm.$route.query.number ;
      method.luckyMans();
      method.weChatShare();
    }
  }
</script>

<style scoped>

</style>



