<template>
    <div class="imgs-box">
        <slick ref="slick" :options="opt">
          <div v-for="item in imgData" class="w100">
            <img :src="item" alt="" class="w100">
          </div>
        </slick>

    </div>
</template>

<script>
  import slick from 'vue-slick';
    export default {
        data(){
          return {
            imgData:[
              "http://oss.zjbird.com/andpath1526874780053worker15268747800530.png?x-oss-process=style/w200",
              "http://oss.zjbird.com/andpath1526874780687worker15268747806861.png?x-oss-process=style/w200",
              "http://oss.zjbird.com/andpath1526874781340worker15268747813392.png?x-oss-process=style/w200",
              "http://oss.zjbird.com/andpath1526875016806worker152687501680419.png?x-oss-process=style/w200"
            ],
            opt:{
              slidesToShow: 1,
              arrows:false,
              autoplay:false
            }
          }
        },
        methods:{

        },
        components:{
          slick
        }
    }
</script>

<style scoped>
  .imgs-box{
    position: fixed;
    bottom: 0;
    top: 0;
    left: 0;
    right: 0;
    background-color: rgba(51,51,51,1);
    z-index: 999999;
  }
</style>



