import http from '../core/http'
import userinfo from '../services/userinfo'
var api = {};
var setToken = function (params) {
  var _params = Object.assign({}, params);
  if (userinfo.store) {
    _params.uid = userinfo.store.uid;
    _params.token = userinfo.store.token;
  }
  return _params;
};
/* *** php的apiv2接口 start *** */

//获取交易流水
api.get_trade = function (params) {
  return http('post', 'http://apiv2.e1.fat.zjbird.com/api/pay/create/trade', params,true);
};

//获取客服电话
api.service_phone = function (params) {
  return http('get', 'http://apiv2.e1.fat.zjbird.com/api/app_config', params,true);
};

