<template>
  <div class="w100 h100 fz14">
    <div class="pa3 mb6 oh-y-a">
      <header class="bgf p43 br3">
        <div class="one-header flex">
          <div class="flex">
            <div class="img-box mr15">
              <img src="../../../assets/images/noheader.png" alt="" class="workerheader">
            </div>
            <div>
              <div class="flex-ac mt3">
                <p class="fz20 mr3">陈辉</p>
                <p class="fz15">晋江体验中心</p>
              </div>
              <div class="flex-ajc mt3">
                <star-rating  :rating = '2.5'
                              :rounded-corners = true
                              :star-points = '[29,8,20,23,6,25,16,40,13,56,29,49,44,56,42,40,52,25,37,23]'
                              :increment = 0.1
                              active-color ='rgb(51, 51, 51)'
                              inactive-color = 'rgb(255,255,255)'
                              border-color = 'rgb(51, 51, 51)'
                              :star-size="17"
                              :show-rating= false
                              :border-width='2'
                > </star-rating>
                <p class=" ml3 mt2 fz15">2.5评分</p>
              </div>

            </div>
          </div>
        </div>
      </header>
      <div class="order-main-box pa3 mt3 br3 bgf">
        <div class="flex-space-between">
          <p class="fz14">服务类型</p>
          <div class="flex-ajc pv1">
            <p class="fz14 c9">选择服务类型</p>
            <div class="img-box ml3" style="width: 1.4rem;" @click="selectService()">
              <img src="../../../assets/images/img/workerCard/icon_tran.png" alt="">
            </div>
          </div>
        </div>
        <div class="border-t mv3"></div>
        <div>
          <p>需求详情</p>
          <textarea class="needs mt3" placeholder="请填写您的详细需求" maxlength="400"></textarea>
        </div>
      </div>
      <div class="order-main-box pv3 mt3 br3 bgf">
        <div class="flex-space-between ph3">
          <p>联系人</p>
          <input type="text" placeholder="请填写联系人姓名" class="tr bn" maxlength="10">
        </div>
        <div class="border-b mv3"></div>
        <div class="flex-space-between ph3">
          <p>联系电话</p>
          <p>123456677777</p>
        </div>
        <div class="border-b mv3"></div>
        <div class="flex-space-between ph3">
          <p class="fz14">服务地区：福建省 晋江市</p>
          <div class="flex-ajc pv1">
            <p class="fz14 c9">选择区县</p>
            <div class="img-box ml3" style="width: 1.4rem;">
              <img src="../../../assets/images/img/workerCard/icon_tran.png" alt="">
            </div>
          </div>
        </div>
        <div class="border-b mv3"></div>
        <div class="ph3">
          <p>详细地址</p>
          <textarea class="needs mt3" placeholder="请填写您的详细地址" maxlength="400"></textarea>
        </div>
      </div>
    </div>
    <a href="javascript:void(0);" class="tac height-btn pf w100 fz17 mt5" :class="isNext ? 'btn-bt-orange' : 'btn-bt-default'">确定预约</a>
  </div>
</template>

<script>
  import StarRating from 'vue-star-rating'
  import api from '../../../services/api'
  import error from '../../../services/error'


  var store = {};
  var method = {};
  store.isNext = false;
  store.workerTypeList = [];//工种列表



  //获取工种
  method.get_requtype = function () {
    api.get_requtype().then(function (res) {
      console.log('工种',res);
      if (res.code == error.success){
        store.serviceTypeList = res.result;
        console.log(store.serviceTypeList);
      }
    })
  };
  //选择服务类型
  method.selectService = function () {
    this.$selectbox.show(
      {'name':'服务类型','ind':true},
      {
        cshow:function (id,name){
          store.worktype = id;
          store.wtypename = name;
          console.log("id",id);
          console.log("name",name);
        },
        cb:function () {


        },
        selectData:store.serviceTypeList,
      }
    );
  };
    export default {
        name: "orderDetail",
        data(){
          return store;
        },
        methods:method,
        components: {
          StarRating
        },
        mounted(){
          method.get_requtype();
        }
    }
</script>

<style scoped>

</style>



