const state = {
    INFO : null
};
const getters = {
    
};
const actions = {
    INFO_FN_ACT : ({commit},msg) =>{
        commit("INFO_FN_MUT",msg)
    }
};
const mutations = {
    INFO_FN_MUT : (state,msg) => {
        state.INFO = msg;
    }
};
export default {
    state,
    getters,
    actions,
    mutations

}


