<template>
    <div class="h100 w100 record-body">
        <div v-if="missMoneys.code === 200">
            <p class="title-bar pft ">已错过¥<span class="font-color">{{missMoneys.result}}</span>邀请补贴,联系服务商获取邀请补贴</p>
        </div>
        <div id="topRecord">
            <img  src="../../assets/images/img/invite/老带新_06.png" alt="">
        </div>
        <div class="invite-info-parent" id="dhWarp">
            <ul class="flex-space-between invite-info" >
                <li class="bg-white flex-clounm-spac-between">
                    <div class="flex-space-between">
                        <img src="../../assets/images/img/invite/朋友 好友 邀请 伙伴@2x.png" alt="">
                        <span class="invite-info-color fz12">已邀工友</span>
                    </div>
                    <div>
                        <span class="fz17 font-color">{{inviteMoney.sum}}</span>
                        <span class="invite-info-color fz10">人</span>
                    </div>
                </li>
                <li class="bg-white flex flex-column flex-clounm-spac-between">
                    <div class="flex-space-between">
                        <img src="../../assets/images/img/invite/钱.png" alt="">
                        <span class="invite-info-color fz12">邀请红包</span>
                    </div>
                    <div>
                        <span class="fz17 font-color">{{inviteMoney.red}}</span>
                        <span class="invite-info-color fz10">元</span>
                    </div>
                </li>
                <li class="bg-white flex flex-column flex-clounm-spac-between">
                    <div class="flex-space-between">
                        <img src="../../assets/images/img/invite/补.png" alt="">
                        <span class="invite-info-color fz12">邀请补贴</span>
                    </div>
                    <div>
                        <span class="fz17 font-color">{{inviteMoney.profile}}</span>
                        <span class="invite-info-color fz10 font-color">元</span>
                    </div>
                </li>
            </ul>
        </div>
        <div class="record-count-info" id="imgHeight">
            <div class="relactive">
            <p class="fz15 title-bar ">你还有<span class="font-color">{{invitCount}}</span>次抽奖机会</p>
            <p class="fz15 font-color prize-active" @click="prizeActive()">抽奖&nbsp;</p>
            <ul class="flex-ajc fz13 lin35" style="border-bottom:1px solid rgba(238, 228, 218, 1)">
                <li class="w20 tac invite-info-color"></li>
                <li class="w20  invite-info-color">工友</li>
                <li class="w20 tac invite-info-color">认证状态</li>
                <li class="w20 tac invite-info-color">邀请红包</li>
                <li class="w20 tac invite-info-color">邀请补贴</li>
            </ul>
            </div>
        <div class="record-scroll h100" :style="{height : fullHeight}" id="bottomTop">
            <div>
                <ul class="w100 flex-ajc  fz13" v-for="item in recordData" :key="item.id">
                    <li class="w20 h100 flex-ajc  recored-count-padding  padding-left "><img style="width:3.5rem;" class=" border-radius-100" :src="item.photo" alt=""></li>
                    <li class="w20 h100 flex-clounm-spac-between fontColor02 recored-count-padding ">
                        <p class="fontColor02">{{item.name}}</p>
                        <p class="fz11 fontColor02">{{item.mobile}}</p>
                    </li>
                    <li class="w20 h100 flex-clounm-start recored-count-padding"><p :class="{fontColor01 : item.rzst === '未通过',fontColor02 : item.rzst !== '未通过'}">{{item.rzst}}</p></li>
                    <li class="w20 h100 flex-clounm-start recored-count-padding"><p class="fontColor01">{{item.invitereds}}</p></li>
                    <li class="w20 h100 flex-clounm-spac-between recored-count-padding  padding-right">
                        <p class="fontColor01">{{item.profile}}</p>
                        <P class="fz11 fontColor02">{{item.status}}</P>
                    </li>
                </ul>
                <div class="w100 li-border-bottom"></div>
            </div>
        </div>
        </div>
        <div class="w100" @click="smallVault()">
            <p class="fz17 w100 flex-ajc record-bottom">去小金库查看详情</p>
        </div>
    </div>
</template>
<script>
import core from '../../core/jsbridge/core'
import api from "../../services/api";

export default {
    data () {
       return {
        fullHeight : 0 ,//计算高度
        workerID : 0,
        missMoneys : 0, //错过金额
        invitCount : 0, //抽奖次数
        inviteMoney : {}, //抽奖次数title
        recordData : {
            img : 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1530088964310&di=f5c684ec4e439804ca682b15785a517d&imgtype=0&src=http%3A%2F%2Fimg3.duitang.com%2Fuploads%2Fitem%2F201407%2F12%2F20140712202600_fMyya.jpeg',
            name : "李狗蛋",
            phone: '155****6666',
            record: "未通过",
            inviteMoney : '10.8元',
            subsidy : '6.66元'

        },
        title_info : { 
                "title":"邀请记录", //邀请记录
                "actionRight":{
                "actionIcon":"",
                "actionText":"活动说明",
                "actionTextColor":"#999999"//ios不传可能闪退
                },
            "bgColor":"#ffffff"
            }
       }
    },
    created () {
        this.workerID = {
            // workerid : this.$store.state.INFO_FN.INFO.user_id
            workerid : 14581,
        };
       this.workerId = {
              workerId : this.$store.state.INFO_FN.INFO.user_id
       },
        this.lotteryCount();
        this.getRecordTitle();
        this.getInviteRecordCount();
        this.missMoney();
    },
    mounted() {
        if (this.$store.state.HEIGHT.HEIGHT) {
            this.fullHeight = this.$store.state.HEIGHT.HEIGHT;
        } else {
            this.computeds();  
        }
    },
    methods : {
        //计算高度
        computeds () {
        let dh= document.querySelector('#topRecord').offsetHeight;
        let dha= document.querySelector('#dhWarp').offsetHeight;
        let dhb = document.querySelector('#imgHeight').offsetHeight;
        let dhc = document.querySelector('#bottomTop').offsetTop;
        let fullHeight = document.querySelector('body').clientHeight;
     
        this.fullHeight = fullHeight-dhc-dh-dha-dhb+ 5 +'px';
        this.$store.commit('COMPUTED_HEIGHT',this.fullHeight);
        //获取原生数据
        core.$app('title_info_control', this.title_info).then(function (res) {
        });
        core.$reg('title_right_click', this.clickRight).then(function (res) {
        }) 
        },

        //活动说明页面跳转
        clickRight() {
            this.$router.push({path : './activeDes'})
        },
        
        //抽奖页面路由
        prizeActive () {
            this.$router.push({path:'/turntable'})
        },

        //调用原生去往小金库
        smallVault () {
            core.$app('go2purses').then(res => {
            })
        },

        //抽奖title
        getRecordTitle () {
            api.inviteRecordTitle(this.workerID).then (res => {
                this.inviteMoney = res.result;
            })
        },

        //邀请记录
        getInviteRecordCount () {
            api.inviteRecordCount(this.workerID).then(res => {
                this.recordData = res.result;
            })
        },

        //获取抽奖次数
        lotteryCount() {
            api.geetLuckyDrowCount(this.workerId).then(res => {
                this.invitCount = res.result.count;
        });
        },

        //是否错过了金额
        missMoney () {
            api.missMoney(this.workerID).then (res => {
                console.log(res);                
                this.missMoneys = res;
            })
        },
    },
}
</script>
<style scoped>
.font-color {
    color: rgba(253, 42, 0, 1);
}
.fontColor01 {
    color: rgba(255, 0, 0, 1);
}
.title-bar {
    line-height: 3.5rem;
    background: rgba(255, 245, 226, 1);
}
.prr {
    position: relative;
    top: 0;
    right: -20%;
}
.fontColor02 {
    color: rgba(118, 74, 35, 1);
}
</style>




