<template>
<div class="fz14 p23 red-text-wrap bgf w100 h100">
  <p>1、优惠券仅限在住建鸟使用，按用户所获得优惠券自身规则进行抵扣。</p>
  <p>2、优惠券获取方式：通过赠送、购买或活动参与等形式获取。</p>
  <p>3、使用优惠券提交的订单，在订单未支付时取消订单，则订单取消后，系统自动返还相应的优惠券；若订单被支付后，取消某一子单或取消全部子单，优惠券均不返还。</p>
  <p>4、已使用优惠券的订单，产生退货退款，对应的优惠券不予返还。</p>
  <p>5、优惠券不能进行兑现、出售、转让或其他用途。</p>
  <p>6、本规则由住建鸟依据国家相关法律法规及规章制度予以解释。</p>
</div>
</template>

<script>
  import core from '../../core/jsbridge/core'
    export default {
        name: "redInstructions",
        mounted(){
          store.title_info = {
            "title": "优惠券使用说明",
            "actionRight": {
              "actionIcon": "",
              "actionText": "",
              "actionTextColor": "#333333"//ios不传可能闪退
            },
            "bgColor": "#ffffff"
          };
          let hander = setInterval(function () {
            if (!window.WebViewJavascriptBridge) {
              return
            }
            else {
              clearInterval(hander)
            }
            core.$app('title_info_control', store.title_info).then(function (res) {
            });
          }, 10);
        }
    }
</script>

<style scoped lang="scss">
.red-text-wrap p{
  margin-top: 0.5rem;
  line-height: 1.8rem;
  text-align: justify;

}
</style>



